<template>
  <nav id="catalog-nav">
    <div style="
    margin-bottom: 15px;
">
      <div style="    margin: 25px 0 5px 0;">
        <img class="head-icon-img" src="data:image/jpeg;base64,">
      </div>
      <div style="font-size: 30px;
    padding-bottom: 10px;">
        <span style="    position: relative;
    left: 70px;">小明同學
        </span>
      </div>
      <div style="    text-align: center;">
        <span style="font-size: 18px;">学习、积累、沉淀、记录、分享</span>
      </div>
      <div style="    margin-top: 30px;">
        <div>
          <span style="    position: relative;
    color: #959da5;
    left: 105px;">瞧一瞧</span>
        </div>
        <div>
          <a href="https://github.com/710241969/my-blog" class="contact-a" style="left: 50px;" target="_blank">
            <i class="fa fa-github fa-2x fa-fw" style="    padding: 5px 0;"></i>
          </a>
          <a class="contact-a" style="  left: 68px;" target="_blank">
            <i class="fa fa-envelope fa-2x fa-fw"></i>
          </a>
          <a class="contact-a" style="    left: 90px;
    top: 2px;" target="_blank">
            <i class="fa fa-wechat fa-2x fa-fw"></i>
          </a>
          <a class="contact-a" style="    left: 90px; display:none;
    top: 2px;" target="_blank">
            <i class="fa fa-home fa-2x fa-fw"></i>
          </a>
        </div>
      </div>
    </div>
    <div>
      <div style="    font-size: 25px;
    height: 30px;
    padding: 10px 0 0 5px;    border-bottom: 1px solid #c6cbd1;">博客目录</div>
      <catalog-tree :catalog-obj="catalogObj"></catalog-tree>
    </div>
  </nav>
</template>

<script>
import CatalogTree from './CatalogTree.vue'

export default {
  data() {
    return {}
  },
  props: {
    catalogObj: {
      type: Object,
      default: ''
      // required: true,
      // validator: () => {}
    }
  },
  computed: {},
  created() {},
  methods: {},
  components: {
    CatalogTree
  }
}
</script>

<style lang="scss" scoped>
.head-icon-img {
  height: 90px;
  width: 90px;
  border-radius: 50%;
  border: 5px solid #444d56;
  position: relative;
  left: 80px;
}

.contact-a {
  // width: 42px;
  // height: 42px;
  color: #fff;
  display: inline-block;
  // padding: 3px 0;
  // padding: 0 20px;
  position: relative;
}

a {
  text-decoration: none;
  cursor: pointer;
}
a:hover {
  text-decoration: none;
}
a:focus {
  text-decoration: none;
}
a:visited {
  text-decoration: none;
}
a:active {
  text-decoration: none;
}

nav {
  position: fixed;
  background: #222629;
  width: 300px;
  height: 100%;
  box-shadow: inset -0.25em 0 0.25em 0 rgba(0, 0, 0, 0.1);
  color: #fff;
  left: 0;
  overflow-y: auto;
  position: fixed;
  text-align: left;
  top: 0;
}

@media screen and (max-width: 700px) {
  nav {
    background: #222629;
    width: 100%;
    height: 200px;
    box-shadow: inset -0.25em 0 0.25em 0 rgba(0, 0, 0, 0.1);
    color: #fff;
    left: 0;
    overflow-y: auto;
    position: fixed;
    text-align: center;
    top: 0;
  }
}

.catalog-nav-div {
  // overflow: auto;
  // position: relative;
  // height: 500px;
}
</style>




