# 打包后静态资源路径问题
2018-04-13 01:52:26
## 问题描述
初始化完vue-cli项目之后，
npm run dev结果很完美
但是当npm run build想美美地感受一下，结果却发现输出框一片红
![](
  data:image/png;base64,)
很生气，我想应该是webpack的配置的问题了

## 问题查证
既然我认为是webpack的配置问题，那么只要修改一下配置就得了，那么问题来了，这vue-cli V3.0，项目结构非常简洁，配置的问题都交给vue-cli-service了，要怎么去配置？&#x1F615;&#x1F615;&#x1F615;
这样只能去看文档了，于是看到官方有这么一段话

> ### vue.config.js
Many aspects of a Vue CLI project can be configured by placing a `vue.config.js` file at the root of your project. The file may already exist depending on the features you selected when creating the project.
`vue.config.js` should export an object, for example:
```js
// vue.config.js
module.exports = {
  lintOnSave: true
}
```

简单翻译一下就是，在项目的根路径下放一个`vue.config.js`文件，然后在里面写配置，就能配置这个vue项目了，当然，初始化创建好这个项目的时候，这个文件可能已经存在，也可能不存在，不存在就自己新建一个呗。

新建完之后，嗯，那webpack的配置又怎么写进来？还是看文档

> If you need conditional behavior based on the environment, or want to directly mutate the config, use a function (which will be lazy evaluated after the env variables are set). The function receives the resolved config as the argument. Inside the function, you can either mutate the config directly, OR return an object which will be merged:
```js
// vue.config.js
module.exports = {
  configureWebpack: config => {
    if (process.env.NODE_ENV === 'production') {
      // mutate config for production...
    } else {
      // mutate for development...
    }
  }
}
```

## 解决办法
原来如此，现在知道怎么在哪配置webpack了，可以先通过console.log之类的，打印一下看看这个config对象
```js
module.exports = {
  configureWebpack: config => {
    console.log(config)
  }
};
```
打印的结果（省略后面很多）
![webpackConfig](
  data:image/jpg;base64,
  )

就是你了！现在是一个反斜杠，会以绝对路径的方式加载，改成**`'./'`**再build一次试试
![](
  data:image/png;base64,
)

搞定收工
  </pre>
</template>

<script>
export default {}
</script>
